import { Component, OnInit } from '@angular/core';
import { Chart } from 'angular-highcharts';

@Component({
  selector: 'hcal-report',
  template: `<h3 class="title-component">{{ componentName }}</h3>
<div class="content graph-container col-4 float-left">
  <div [chart]="chart" class="col-12 chart-content float-left"></div>
</div>
<div class="col-6 offset-1 float-left">
  <ng-content select="[table-report]"></ng-content>
</div>
`,
  styles: [`.graph-container{height:calc(100% - 140px)!important}`]
})
export class ReportComponent implements OnInit {

  componentName: string;

  chart = new Chart({
    chart: {
      type: 'pie'
    },

    title: {
      text: 'Graph',
    },

    credits: {
      enabled: false
    },

    series: [{
      name: 'Pie 1',
      data: [1, 2, 3]
    }]
  });

  constructor() {
    this.componentName = this.constructor.name;
  }

  ngOnInit() {
  }
}
