  styles: [`.menu-item{margin-top:8px;margin-bottom:-3px;height:auto}.menu-item :hover{background-color:rgba(0,171,165,.45);transition:.7s}.menu-item .full-link{display:block;margin-left:-7.5px;text-decoration:none;text-align:center;font-family:"Roboto Medium",sans-serif;color:#00aba5}.menu-item .full-link:hover{color:#fff;background:rgba(0,171,165,.45)}.menu-item .full-link .icon-aside,.menu-item .full-link .title-link{display:block}.menu-item .full-link .icon-aside:hover,.menu-item .full-link .title-link:hover{background:0 0}.menu-item .full-link .icon-aside{margin-bottom:-7px;font-size:3em}.menu-item .full-link .title-link{padding-bottom:5px;width:auto;font-size:.9em;letter-spacing:.9px;font-weight:400}.active{background:rgba(0,171,165,.45)}.active .full-link{color:#fff}`]
