import { Component, Input, OnInit } from '@angular/core';
import { DualListComponent } from 'angular-dual-listbox';

@Component({
  selector: 'hcal-custom-dual-list',
  template: `<dual-list [source]="data" [destination]="fieldDestination" [format]="format"></dual-list>
`,
  styles: [``]
})
export class CustomDualListComponent extends DualListComponent implements OnInit {

  format = {
    add: '', remove: '', all: 'Todos', none: 'Nenhum',
    direction: DualListComponent.LTR, draggable: true, locale: 'br'
  };

  @Input() data: any[];
  fieldDestination = [];

  ngOnInit(): void {
  }

}
